import React from 'react';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Button from 'react-bootstrap/Button';
import { Col, Form } from "react-bootstrap";
import { updateDefault } from "../../redux/actions";

import { connect } from 'react-redux';

class WidgetSettings extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false
        };
    }

    handleAddWidget = widget => {
        widget.inUse = !widget.inUse;
        this.props.updateDefault(widget);
      };    

    getSettingsTitle() {
        if(!this.props.selectedWidget){
            return (
                <h2 className="">No Widget Selected</h2>
            );
        }
        return (
            <h2 className="">{this.props.selectedWidget.name} Settings</h2>
        );
    }

    checkInUse(){
        if(this.props.selectedWidget){
            return this.props.selectedWidget.inUse;
        }

        return true;
    }

    render() {
        if(!this.props.selectedWidget){
            return (
                <h2 className="">No Widget Selected</h2>
            );
        }
        return (
            <div className="p-3">
                {this.getSettingsTitle()}
                <p>Manage widget layout settings</p>
                <Button disabled={this.checkInUse()} onClick={ () => this.handleAddWidget(this.props.selectedWidget)}>Add Widget</Button>
                <Form className="my-2">
                    <Form.Row>
                        <Form.Group as={Col} controlId="formGridEmail">
                            <Form.Label>Width</Form.Label>
                            <Form.Control type="number" placeholder="Set value" value={this.props.selectedWidget.layout.w}/>
                        </Form.Group>

                        <Form.Group as={Col} controlId="formGridPassword">
                            <Form.Label>Height</Form.Label>
                            <Form.Control type="number" placeholder="Set Value" value={this.props.selectedWidget.layout.h}/>
                        </Form.Group>
                    </Form.Row>
                    <Form.Row>
                        <Form.Group as={Col} controlId="formGridEmail">
                            <Form.Label>Min Width</Form.Label>
                            <Form.Control type="number" placeholder="Set Value" value={this.props.selectedWidget.layout.minW}/>
                        </Form.Group>

                        <Form.Group as={Col} controlId="formGridPassword">
                            <Form.Label>Min Height</Form.Label>
                            <Form.Control type="number" placeholder="Set Value" value={this.props.selectedWidget.layout.minH}/>
                        </Form.Group>
                    </Form.Row>
                    <Form.Row>
                        <Form.Group as={Col} controlId="formGridEmail">
                            <Form.Label>Max Width</Form.Label>
                            <Form.Control type="number" placeholder="Set Value" value={this.props.selectedWidget.layout.maxW}/>
                        </Form.Group>

                        <Form.Group as={Col} controlId="formGridPassword">
                            <Form.Label>Max Height</Form.Label>
                            <Form.Control type="number" placeholder="Set Value" value={this.props.selectedWidget.layout.maxH}/>
                        </Form.Group>
                    </Form.Row>
                </Form>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    var selectedWidget = state.selectedWidget;

    return { selectedWidget: selectedWidget }
}

export default connect(
    mapStateToProps,
    {
        updateDefault: updateDefault
    }
)(WidgetSettings);