import {ADD_WIDGET, UPDATE_DEFAULT, SET_DEFAULT, SELECTED_WIDGET} from './actionTypes'

export function addWidget(widget) {
    return {
        type: ADD_WIDGET,
        payload: {
            id: widget.id,
            widget
        }
    }
};

export function updateDefault(widget) {
    return {
        type: UPDATE_DEFAULT,
        payload: {
            id: widget.id,
            widget
        }
    }
};

export function setDefault(widget) {
    return {
        type: SET_DEFAULT
    }
};

export function selectWidget(widget) {
    return {type: SELECTED_WIDGET, payload: {
            widget
        }}
};