import React from 'react';
import Card from 'react-bootstrap/Card';
import Accordion from 'react-bootstrap/Accordion';
import { Col, Form } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEllipsisH, faPlus } from '@fortawesome/free-solid-svg-icons';
import { addWidget } from "../../redux/actions";
import { connect } from 'react-redux';

class WidgetListItem extends React.Component {

    handleAddWidget = widget => {
        widget.inUse = !widget.inUse;
        this.props.addWidget(widget);
    };

    render() {
        return (
            <div className="d-flex flex-row justify-content-between">
                <div><FontAwesomeIcon icon={faPlus} className="fa-2x" onClick={() => this.handleAddWidget(this.props.widget)} /></div>

                <div><h5>{this.props.widget.name}</h5></div>
            </div>
        );
    }
}

export default connect(
    null,
    {
        addWidget: addWidget
    }
)(WidgetListItem);