import React from 'react';
import WidgetListItem from './widget-list-item';
import ListGroup from 'react-bootstrap/ListGroup';

import { selectWidget } from "../../redux/actions";


import { connect } from 'react-redux';

class BackendWidgetList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false
        };
    }

    toggleToolbox() {
        this.setState({ open: !this.state.open })
    }

    ifListEmpty() {
        if (!this.props.widgets || !this.props.widgets.length) {
            return (
                <h4 className="text-white text-center">Empty</h4>
            );
        }
    }

    handleAddWidget = widget => {
        this.props.selectWidget(widget);
    };

    render() {
        return (
            <div className="h-100 bg-white">
                <ListGroup defaultActiveKey="#link1">
                    {this.props.widgets.sort((a, b) => a.id - b.id).map(item => (
                        <ListGroup.Item key={item.id} variant="light" action onClick={() => this.handleAddWidget(item)}>{item.name}</ListGroup.Item>
                    ))}
                </ListGroup>,
            </div>
        );
    }
}

const mapStateToProps = (state) => {

    return { widgets: state.defaultLayout.sort((a, b) => a.name.localeCompare(b.name)) }
}

export default connect(
    mapStateToProps,
    {
        selectWidget: selectWidget
    }
)(BackendWidgetList);